export default{
	data(){
		return{
			height:'',
		}
	},
	onLoad(){
		this.getTopheight();
	},
	methods:{
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.top = 65 + "px";
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
	}
}
